import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
} from '@umetrics/sartorius-ui-base';

import add from '@umetrics/sartorius-ui-icon/dist/icons/add';
import minus from '@umetrics/sartorius-ui-icon/dist/icons/minus';
import { counterStyles } from './counterStyle';
import { CounterProperties, CounterSize } from './counterProperties';
import { version } from './metadata';
import { CounterEvent } from './counterEvent';

@defineElement({
    tagName: 'counter',
    styles: counterStyles,
    version,
    //    sub: [TextField, Button, Icon]
})
export class Counter extends BaseElement implements CounterProperties {
    // Static members
    private static TextField;
    private static Button;
    private static Icon;

    @property({ type: Number, reflect: false, attribute: 'upper-bound' })
    upperBound = 9999;
    @property({ type: Number, reflect: false, attribute: 'lower-bound' })
    lowerBound = 0;
    @property({ type: Number, reflect: false, attribute: 'step-size' })
    stepSize = 1;
    @property({ reflect: false, attribute: false, type: Array<number>() })
    steps: number[] = [];
    @property({ type: Boolean, converter: booleanConverter, reflect: false })
    strict = false;
    @property({ type: String, reflect: false })
    unit = '';
    @property({ type: Boolean, converter: booleanConverter })
    disabled = false;
    @property({ type: Boolean, converter: booleanConverter })
    error = false;
    @property({ type: CounterSize, reflect: true })
    size = CounterSize.Medium;
    @property({ type: Number })
    width;

    @property({ type: Number, reflect: true })
    get value() {
        return this._value;
    }

    set value(value: number) {
        const prevValue = this.value;
        if (this._setValue(value, prevValue)) {
            this.requestUpdate('value', prevValue);
        }
    }

    private _value: number = this.lowerBound | 0;
    private _decimalSize = 0;

    constructor() {
        super();
        this._handleMinus = this._handleMinus.bind(this);
        this._handlePlus = this._handlePlus.bind(this);
        this._handleInput = this._handleInput.bind(this);
        this._handleChange = this._handleChange.bind(this);

        this._handleNextStep = this._handleNextStep.bind(this);
        this._handlePrevStep = this._handlePrevStep.bind(this);
        this._handleTexFieldSizeChange = this._handleTexFieldSizeChange.bind(this);
    }

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-button'), elem: ['Button'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-text-field'), elem: ['TextField'] });

        Counter.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
    }

    render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            error: this.error,
            [this.size]: true,
        });
        const textFieldWidth = this._handleTexFieldSizeChange();

        return html`
            <div class="${rootClasses}">
                <div class="${baseClassName}-button-wrapper">
                    <${Counter.Button.tag}
                        class="${baseClassName}-decrease"
                        compact="true"
                        type="secondary"
                        disabled="${
                            this.disabled ||
                            (this.steps.length === 0 ? this.value === this.lowerBound : this.value <= this.steps[0])
                        }"
                        @click="${this.steps.length === 0 ? this._handleMinus : this._handlePrevStep}"
                        full-height
                        full-width
                        data-testid="decrease">
                        <${Counter.Icon.tag}
                            slot="icon-slot"
                            icon=${minus}></${Counter.Icon.tag}>
                    </${Counter.Button.tag}>
                </div>
                <${Counter.TextField.tag}
                    style="
                        --textfield-input-padding: ${this.unit === '' ? '10.5px 15px' : '10.5px 6px'}; 
                        --textfield-root-width: ${this.width ? this.width + 'px' : textFieldWidth}; 
                        --textfield-root-min-width: ${textFieldWidth};"
                    class="${baseClassName}-text"
                    force-unit
                    .value=${this._value}
                    disabled="${this.disabled}"
                    unit="${this.unit}"
                    @input="${this.strict ? null : this._handleInput}"
                    @change="${this._handleChange}"
                    size="${this.size}"
                    data-testid="input">
                </${Counter.TextField.tag}>
                <div class="${baseClassName}-button-wrapper">
                    <${Counter.Button.tag}
                        class="${baseClassName}-increase"
                        compact="true"
                        type="secondary"
                        disabled="${
                            this.disabled ||
                            (this.steps.length === 0
                                ? this.value === this.upperBound
                                : this.value >= this.steps[this.steps.length - 1])
                        }"
                        @click="${this.steps.length === 0 ? this._handlePlus : this._handleNextStep}"
                        full-height
                        full-width
                        data-testid="increase">
                        <${Counter.Icon.tag}
                            slot="icon-slot"
                            icon=${add}></${Counter.Icon.tag}>
                    </${Counter.Button.tag}>
                </div>
            </div>
        `;
    }

    protected firstUpdated(_changedProperties) {
        super.firstUpdated(_changedProperties);
        if (this.steps.length !== 0 && !this.value) {
            this.value = this.steps[0];
        }
        this._decideDecimal();
    }

    private _handleInput(e) {
        if (isNaN(e.target.value)) {
            this.error = true;
        }
    }

    private _handleChange(e) {
        e.preventDefault();
        e.stopPropagation();

        const prev = this.value;
        if (this._setValue(e.target.value, prev)) {
            this._dispatchEvent(CounterEvent.Change);
            this.requestUpdate('value', prev);
        }
    }

    private _setValue(newValue, prevValue) {
        const value = this._strictValue(newValue);

        if (value <= this.lowerBound) {
            this._value = this.lowerBound;
            this.error = false;
        } else if (value >= this.upperBound) {
            this._value = this.upperBound;
            this.error = false;
        } else if (!isNaN(value)) {
            this._value = Number(value);
            this.error = false;
        } else {
            this.error = true;
            return false;
        }
        if (prevValue !== this._value) {
            return true;
        }
        if (prevValue !== newValue) {
            // Set strict and steps 1. Enter for example 14.5, cause change to 14. Enter 14.5 again. This is "no change" but we need to update the input field.
            const text = this.shadowRoot!.querySelector('[class*="-text"]') as HTMLInputElement;
            text.value = ''; // Force update
            text.value = prevValue;
        }
        return false;
    }

    private _strictValue(value) {
        if (this.strict && this.steps.length !== 0) {
            const closest = this.steps.reduce((a, b) => {
                return Math.abs(b - value) < Math.abs(a - value) ? b : a;
            });
            return closest;
        } else if (this.strict && this.steps.length === 0) {
            return Math.round(value / this.stepSize) * this.stepSize;
        }
        return value;
    }

    private _handleNextStep() {
        const currentIndex = this.steps.indexOf(Number(this.value), 0);
        if (currentIndex < this.steps.length - 1 && currentIndex !== -1) {
            this.value = this.steps[currentIndex + 1];
        } else {
            const higherValues = this.steps.filter((higher) => higher > this.value);
            let closest = Infinity;
            higherValues.forEach((val) => {
                if (val < closest) closest = val;
            });
            if (this.value <= this.steps[this.steps.length - 1]) {
                this.value = closest;
            } else {
                this.value = this.steps[this.steps.length - 1];
            }
        }
        this._dispatchEvent(CounterEvent.Change);
    }

    private _handlePrevStep() {
        const currentIndex = this.steps.indexOf(Number(this.value), 0);
        if (currentIndex > 0 && currentIndex !== -1) {
            this.value = this.steps[currentIndex - 1];
        } else {
            const lowerValues = this.steps.filter((lower) => lower < this.value);
            let closest = 0;
            lowerValues.forEach((val) => {
                if (val > closest) closest = val;
            });
            if (this.value >= this.steps[0]) {
                this.value = closest;
            } else this.value = this.steps[0];
        }
        this._dispatchEvent(CounterEvent.Change);
    }

    private _handleMinus() {
        if (this.lowerBound < this.value) {
            this.value = Number((this.value - this.stepSize).toFixed(this._decimalSize));
        } else {
            this.value = this.lowerBound;
        }
        this._dispatchEvent(CounterEvent.Change);
    }

    private _handlePlus() {
        if (this.upperBound > this.value) {
            this.value = Number((this.value + this.stepSize).toFixed(this._decimalSize));
        } else {
            this.value = this.upperBound;
        }
        this._dispatchEvent(CounterEvent.Change);
    }

    private _decideDecimal() {
        if (this.stepSize >= 1) {
            this._decimalSize = 1;
        }
        if (this.stepSize <= 0.1 && this.stepSize >= 0.01) {
            this._decimalSize = 2;
        } else {
            this._decimalSize = 3;
        }
    }

    private _dispatchEvent(event) {
        this.dispatchEvent(
            new CustomEvent(event, {
                detail: {
                    option: this,
                    value: this._value,
                },
                bubbles: true,
                cancelable: true,
            })
        );
    }

    private _handleTexFieldSizeChange() {
        if (this.unit === '' && this.size === CounterSize.Medium) {
            return '68px';
        } else if (this.unit !== '' && this.size === CounterSize.Medium) {
            return '85px';
        } else if (this.unit === '' && this.size === CounterSize.Small) {
            return '67px';
        } else if (this.unit !== '' && this.size === CounterSize.Small) {
            return '84px';
        }

        return '';
    }
}
