import { version } from './metadata';

let icons;
const getStash = () => {
    if (!icons) {
        let studio = window['studio'];
        if (!studio) {
            studio = window['studio'] = {};
        }
        let sui = studio['sui'];
        if (!sui) {
            sui = studio['sui'] = {};
        }
        const major = version.split('.')[0] || version;
        const stash = `icons${major}`;
        icons = icons || sui[stash];
        if (!icons) {
            icons = sui[stash] = {};
        }
    }
    return icons;
};

export const registerIcon = (data) => {
    const stash = getStash();
    if (!stash[data.name]) {
        stash[data.name] = data.data;
    }
    return data.name;
};

export const fetchIcon = (icon) => {
    return getStash()[icon];
};
