export interface CounterProperties {
    upperBound?: number;
    lowerBound?: number;
    stepSize?: number;
    steps?: number[] | string;
    unit?: string;
    disabled?: boolean;
    error?: boolean;
    value?: number;
    width?: number;
    size?: CounterSize;
    strict?: boolean;
}

export enum CounterSize {
    Medium = 'medium',
    Small = 'small',
}
