import { css, CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const counterStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const yellow = cssVariableAsVar('color_primary_1');

    return [
        css`
            ${root} {
                display: flex;
                flex-direction: row;
            }

            ${child('text')} {
                --textfield-input-min-width: 30px;
            }

            ${child('wrapper')} {
                display: inline-block;
                position: relative;
            }

            ${is('error')} {
                --textfield-input-border: 1px solid ${cssVariableAsVar('color_traffic_light_1')};
                --textfield-input-border-focused: 1px solid ${cssVariableAsVar('color_traffic_light_1')};
            }

            ${child('description')} {
                margin-left: 16px;
                font-size: 14px;
                color: ${cssVariableAsVar('color_secondary_7')};
            }

            ${child('button-wrapper')} {
                user-select: none;
            }

            ${is('medium')} ${child('button-wrapper')} {
                height: 40px;
                width: 40px;
            }

            ${is('small')} ${child('button-wrapper')} {
                height: 32px;
                width: 32px;
            }

            @media (hover: hover) {
                ${root}:hover input:not(:disabled) ~ ${child('thumb')} {
                    outline: ${yellow} solid 6px;
                }
            }
        `,
    ] as CSSResultArray;
};
